import { HBModalConfirm } from 'hbBaseClass';
import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { hiddenPopupPayment } from '../../../store/payment/actions';

const CheckPayment: React.FC = (props) => {
  const history = useHistory();
  const { show } = useSelector((state: any) => state.payment);
  const { message } = useSelector((state: any) => state.payment);
  const dispatch = useDispatch();

  /**
   * handle close popupconfirm
   */
  const handleClose = () => {
    dispatch(hiddenPopupPayment());
    history.push('/mobility/posture');
  };

  /**
   * create customer id for stripe
   */
  const handleDirectPayment = () => {
    history.push(`/payment-direct`);
    dispatch(hiddenPopupPayment());
  };

  return (
    <HBModalConfirm
      handleClose={handleClose}
      show={show}
      // "Bitte abbonieren Sie zuerst unsere Videos, um Zugang zu den Inhalten zu bekommen"
      content={message}
      title="Abonnement"
      up="Zahlungsart einrichten"
      upCallback={handleDirectPayment}
      down="Abbrechen"
      downCallback={handleClose}
    />
  );
};
export default CheckPayment;
